.catalog {
   background: #fff;
   &__container {
      padding: 50px 10px 0px 10px;
      @include small-tablet{
         padding: 100px 10px 0px 10px;
      }
   }
   &__body {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      @include tablet{
         padding: 0 15px;
      }
      @include small-tablet{
         flex-direction: column;
      }
   }
   @include small-tablet{
      position: fixed;
      top:0;
      left: 0;
      width: 100vw;
      height: 100vh;
      z-index: 970;
      background-color: #333333;
      transform: translateY(100%);
      transition-duration: .3s;
   }
   &_active{
      transform: translateY(0);
   }
}

.filter {
   @include mr(10px);
   min-width: 130px;
   max-width: 160px;
   position: relative;
   &__button {
      width: 100%;

      display: flex;
      align-items: center;
      justify-content: space-between;

      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: #333333;
      text-align: left;

      padding: 10px 0;

      border: none;
      border-bottom: 1px solid #D6D6D6;
      background-color: #fff;
      outline: none;
      cursor: pointer;
      &::after{
         content: '';
         width: 0;
         height: 0;
         border-style: solid;
         border-width: 10px 6px 0 6px;
         border-color: #333333 transparent transparent transparent;
         pointer-events: none;
      }
      @include small-tablet{
         color:#fff;
         background: #333333;
         &::after{
            content: '';
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 6px 0 6px 10px;
            border-color: transparent transparent transparent #ffffff;
            pointer-events: none;
         }
      }
   }

   &__group {
      background: #fff;
      display: none;
      width: 100%;
      position: absolute;
      left: 0;
      top: 50px;
      box-shadow: 0 5px 5px rgba($color: #000000, $alpha: .2);
      overflow: hidden;
      z-index: 910;
      @include small-tablet{
         left: 170px;
         top: 10px;
         &:not(fitst-child){
            top: -5px;
         }
         box-shadow: 0 0px 5px rgba($color: #000000, $alpha: .2);
         background:#3b3b3b;
         color:#fff;
         box-shadow: none;
      }
      &_visible{
         display: block;
      }
   }

   &__option {
      cursor: pointer;
      width: 100%;
      padding: 5px;
      &:hover{
         background: rgba(255, 215, 215,.5);
      }
   }
   &__input{
      display: none;
   }
   @include small-tablet{
      @include mb(10px)
   }
   @include small-tablet{

   }
}
.filter__button_btn{
   position: absolute;
   top: -30px;
   right: 15px;
   color:#333333;
   font-size: 30px;
   z-index: 971;
   transition-duration: .3s;
   @media (min-width: 768px){
      display: none;
   }
   &_active{
      color:#fff;
      top: -125px;
   }
}
.filter__btn_search{
   display: none;
   color:#fff;
   padding: 10px 5px;
   border: 1px solid #fff;
   min-width: 150px;
   margin: 25px auto 0px auto;
   @media (max-width: 767px){
      display: block;
   }
}
.catalog__path{
   margin: 30px 0px 0px 0px;
}