.burger{
   position: absolute;
   right: 0px;
   top: 0px;
   z-index: 901;
   display: none;
   @include small-tablet{
      display: block;
   }
}


.burger-menu{
   position: fixed;
   display: none;
   left: 0;top: 0;
   width: 100vw;
   height: 100vh;
   z-index: 900;
   background: #333333;
   overflow: none;
   color:#fff;
   &__container {
      padding-top: 142px;
   }

   &__body {
   }

   &__content {
      width: 100%;
   }

   &__list {
      margin: 0px 0 35px 0;
      display: flex;
      justify-content: space-between;
      flex-direction: column;
   }

   &__item {
      @include mb(30px)
   }

   &__link {
      color:#fff;
      font-weight: 400;
      font-size: 21px;
      line-height: 140%;
   }
   &__links {
      width: 40px;
      height: 40px;
      @include flex-all-center
      
   }

   &__nav {
      padding: 33px 0;
      margin: 0px 0px 0px 8px;
      display: flex;
   }

   &__font {
      a{
         color:#fff;
      }
   }

   &__contacts {
      margin: 25px 0px 0px 0px;
      display: flex;
   }

   &__contact-item {
      @include mr(30px);
      @include b340{
         @include mr(20px)
      }
   }
   .fa-solid {
   }
   .fa-user {
      margin: 0px 43px 0px 0px;
      font-size: 35px;
      
   }
   .fa-heart {
      font-size: 35px;
   }
   hr{
      width: 100%;
      height: 1px;
      background-color: #fff;
   }
}
.hide{
   display: block;
}



