.footer {
   padding: 120px 0 50px 0;
   &__container {
   }
   &__body {
      max-width: 1100px;
      display: flex;
      justify-content: space-between;
      @media (max-width: 1070px){
         flex-wrap: wrap;
      }
   }

   &__items {
      flex: 0 1 255px;
      padding: 0 20px;
      @media (max-width: 1070px){
         flex: 0 1 50%;
         @include mb(20px);
      }
      @include small-tablet{
         flex: 0 1 100%;
         @include mb(20px);
         //padding: 0 20px;
      }
   }

   &__title {
      font-weight: 500;
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.1em;
      text-transform: uppercase;
   }

   &__ul {
   }

   &__item {
      @include mb(10px);
      &_cont {
         display: flex;
      }
   }
   &__img {
      width: 45px;
      height: 30px;
      @include mr(15px);
      object-fit: contain;
   }
   &__link {
      color:#333333;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      &_cont {
         height: 30px;
      }
      &_img{
         @include mr(20px);

      }
      &_tel {
         
      }
      &_tel::before{
         font-family: "Font Awesome 5 Free";
         content: "\f879";
         font-weight: 900;
         margin: 0px 20px 0px 0px;
      }
      &_mail::before {
         font-family: "Font Awesome 5 Free";
         content: "\f0e0";
         font-weight: 900;
         margin: 0px 20px 0px 0px;
         
      }
   }

   
   hr{
      width: 100%;
      height: 1px;
      background: #333333;
   }
}