.events{
   background: #F9F9F9;
   padding: 100px 0px 120px 0px;
   @include mobile{
   padding: 60px 0px 70px 0px;
   }
   &__container {
   }

   &__body {
   }

   &__title {
      color: #333333;
      margin: 0px 0px 15px 0px;
   }

   &__subtitle {
      color: #333333;
      margin: 0px 0px 60px 0px;
   }
}

.tabs {

   &__nav {
      display: flex;
      max-width: 100%;
      padding: 0;
      flex-wrap: wrap;
      justify-content: flex-start;
      @include mobile{
         justify-content: center
      }
   }
   &__nav-item {
      @include mr(15px);
      width: 171px;
      height: 61px;
      position: relative;
      margin: 0px 0px 10px 0px;
      @include tablet{
         @include mr(10px);
         width: 156px;
      }
      @include mobile{
         margin-right: 10px;
      }
   }

   &__nav-btn {
      background: #fff;
      border: 1px solid #D6D6D6;
      text-transform: uppercase;
      position: absolute;
      z-index: 50;
      width: 95%;
      height: 55px;
      top: 0;
      left: 0;
      transition-duration: .3s;
      &:hover{
         transition-duration: .3s;
         cursor: pointer;
         background: rgb(211, 210, 210);
      }
   }

   &__content {
   }

   &__nav-background{
      background: #fff;
      display: block;
      position: absolute;
      width: 95%;
      height: 55px;
      border: 1px solid #D6D6D6;
      right: 0;
      bottom: 0;
      z-index: 5;
   }
   &__nav-btn--active{
      color:#fff;
      background: #333333;
      width: 100%;
      height: 61px;
      &:hover{
         background: #333333;
      }
   }
   .tabs__nav-btn:focus{
      background: #333333;
   }

   &__panel {
      margin: 45px 0px 0px 0px;
   }
}


.panel-tabs {

   &__container {
      max-width: 100%;
      margin: 0 auto;
      display: grid;
      grid-template-columns: repeat(3, minmax(280px,350px));
      grid-template-rows: repeat(2, minmax(201px,280px));
      gap: 30px;
      @include tablet{
         grid-template-columns: repeat(2, minmax(250px,350px));
         grid-template-rows: repeat(3, minmax(201px,280px));
         gap: 20px;
         align-content: center;
         justify-content: center;
      }
      @include mobile{
         grid-template-columns:minmax(250px,350px);
         grid-template-rows: repeat(6, minmax(201px,280px));
         gap: 20px;
      }
   }

   &__item {
      height: 100%;
      width: 100%;
      position: relative;
      background: #333333;
      //max-width: 350px;
      //max-height: 280px;
   }

   &__img {
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 5;
      img{
         object-fit: cover;
         width: 100%;
         height: 100%;
      }
   }

   &__title {
      z-index: 6;
      width: 100%;
      height: 28px;
      text-align: center;
      position: absolute;
      bottom: 30px;
      padding: 0;
      margin: 0;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 24px;
      line-height: 116%;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: #FFFFFF;
   }
}

