.card-page {
   padding: 30px 15px 150px 15px;
   position: relative;
   &__container {
      
   }
}

.catalog {

   &__content {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
   }

   &__card {
      flex: 1 1 33.3333%;
      @include flex-all-center;
      padding: 0 10px;
      @include small-tablet{
         flex: 1 1 50%;
      }
      @include mobile{
         flex: 1 1 100%;
      }
   }
}
.card {
   @include small-tablet{
      margin: 0px 0px 20px 0px;
   }
   @include mobile{
      margin: 0px 0px 30px 0px;
   }
   &__body {
   }

   &__link {
   }

   &__img {
      margin: 0px 0px 25px 0px;
   }

   &__tag {
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      text-align: center;
      color: #333333;
      margin: 0px 0px 10px 0px;
   }
   &__title {
      font-weight: 600;
      font-size: 24px;
      line-height: 140%;
      text-align: center;
      color: #333333;
      margin: 0px 0px 50px 0px;
   }
   &__price {
      font-weight: 400;
      font-size: 18px;
      line-height: 117%;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      color: #FFFFFF;
      background: #333333;
      text-align: center;
      padding: 10px 20px;
      max-width: 135px;
      display: block;
      margin: 0 auto;
      transition-duration: .3s;
         &:hover{
         transition-duration: .3s;
         background: #5e5e5e;
      }
   }

   &__btn {
      font-weight: 500;
      font-size: 18px;
      line-height: 117%;
      text-align: center;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: #FFFFFF;
      background: #333333;
      padding: 15px 35px;
      max-width: 250px;
      display: block;
      border: none;
      margin: 60px auto 0 auto;
      cursor: pointer;
      transition-duration: .3s;
         &:hover{
         transition-duration: .3s;
         background: #5e5e5e;
      }
   }

   &__info {
      margin: 150px auto 0px auto;
      padding: 0 15px;
      display: flex;
      justify-content: space-between;
      max-width: 1100px;
      @include small-tablet{
         flex-direction: column;
         justify-content: center;
      }
   }

   &__text {
   }

   &__advantages {
   }

}

.text-card {
   transition-duration: .3s;
   flex: 0 1 540px;
   position: relative;
   &__title{
      font-weight: 600;
      font-size: 30px;
      line-height: 140%;
      color: #333333;
      margin: 0px 0px 30px 0px;
   }
   @include small-tablet{
      flex: 0 1 240px;
      margin: 0px 0px 30px 0px;
   }
   &__text {
      overflow: hidden;
      width: 100%;
      z-index: 2;
      max-height: 240px;
   }

   &__button {
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 2;
      border-bottom: 1px dotted #333333;
      color:#333333;
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 117%;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      display: inline;
      z-index: 21;
   }
   &__button_disabled{
      display: none;
   }
   &__lable{
      position: absolute;
      height: 240px;
      bottom: 0;
      left: 0;
      width: 100%;
      z-index: 20;
      background: linear-gradient(180deg, rgba(249, 249, 249, 0) 0%, #fff 82.29%);
   }
}
.text-card_active {
   padding: 0px 0px 25px 0px;
   .text-card__text {
      max-height: 100%;
   }

   .text-card__lable {
      display: none;
   }
   .text-card__button_active{
      display: none;
   }
   .text-card__button_disabled{
      display: block;
      max-width: 90px;
   }

}

.advantage {
   display: grid;
   grid-template-columns: repeat(2,127px);
   grid-template-rows: repeat(2,133px);
   row-gap: 30px;
   column-gap: 25px;
   @include small-tablet{
      margin: 0 auto;
   }
   &__container {
      //display: flex;
      justify-content: center;
   }

   &__img {
      height: 76px;
      @include flex-all-center;
      margin: 0 auto;
      img{

      }
   }
   &__title{
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      text-align: center;
      color:#333333;
   }

}
.hr{
   flex: 0 0 100%;
   display: block;
   height: 1px;
   width: 100%;
   margin: 60px 0;
   background: #D6D6D6;
   @include small-tablet{
      display: none;
   }
}
.hiden{
   opacity: 0;
}
.scrollDisable{
   overflow: hidden;
}