// базовые подключения

@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// подключения компонентов страницы
@import "./components/header";
@import "./components/burger";
@import "./components/events";
@import "./components/visit";
@import "./components/advice";
@import "./components/contcts";
@import "./components/mailing";
@import "./components/footer";


@import "./components/catalog";
@import "./components/card";
@import "./components/productItem";

@import "./components/contact";

.main{
   background: url('../img/main/background.jpg') center top no-repeat;
   background-size: cover;
   @include small-tablet{
      padding: 0px 0px 119px 0px;
   }
   &__container{
      position: relative;
   }
	&__body {
      margin: 523px auto 0px auto;
      max-width: 432px;
      @include mobile{
         margin: 250px auto 0px auto;
      }
      @include big-desktop{
         margin: 200px auto 0px auto;
      }
      @include tablet{
         margin: 180px auto 0px auto;
      }
	}

	&__title {
      font-family: var(--font-familySecond);
      font-weight: 700;
      font-size: 36px;
      line-height: 136%;
      text-align: center;
      color:#fff;
      margin: 0px 0px 50px 0px;
	}

	&__button {
      margin: 0 auto;
      padding: 15px 35px;
      border: 1px solid #fff;
      background-color: #333333;
      max-width: 270px;
      text-align: center;
	} 
   &__link{
      font-weight: 500;
      font-size: 18px;
      line-height: 138%;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: #fff;
   }
   &__sponsors {
   }
   &__hr {
      margin: 80px 0px 0px 0px;
      width: 100%;
      height: 1px;
      background: #fff;
      display: block;
      @include small-tablet{
         display: none;
      }
   }
}
.sponsors-main {
   padding: 30px 0px 30px 0;
   display: flex;
   flex-wrap: wrap;
   align-items: center;
   //justify-content: space-between;
   &__item:not(:last-child){
      width: 169px;
      border-right: 1px solid #fff;
      @include mr(15px);
      flex: 1 1 auto;
      @media (max-width: (1100px)) {
         max-width: 150px;
      }
      @include tablet{
         flex: 1 1 auto;
         max-width: 150px;
         border-right: none;
         @include mr(0);
         margin: 0px 0px 10px 0px;
      }
   }
   &__link {
      height: 100%;
   }
   &__img {
   }
   @include small-tablet{
      display: none;
   }
   @include tablet{
      height: 120%;
      align-items: center;
      justify-content: space-around;
      max-width: 50%;
      margin: 0 auto;
   }
}
.title{
   font-weight: 400;
   font-size: 21px;
   line-height: 140%;
   text-align: center;
   @include small-tablet{
      font-size: 18px;
   }
}
.subtitle{
   font-family: var(--font-familySecond);
   font-weight: 700;
   font-size: 30px;
   line-height: 137%;
   text-align: center;
   @include small-tablet{
      font-size: 24px;
   }
}
.text {
   margin: 0px 0px 60px 0px;
}
.main-catalog{
   background: #333333;
   @include tablet{
      padding: 0;
   }
   &__container{
      
   }
}