.burger { @include burger }
.header{
   position: relative;
   padding: 25px 0px 25px 0px;
   &__container{
      position: relative;
      @include flex-all-sb;
   }
   &__nav {
      @include small-tablet{
         display: none;
      }
   }
   &__logo {
      z-index: 999;
      width: 216px;
      @include tablet {
         width: 150px;
      }
   }
}

.nav {
   &__right>* {
      @include mr(40px);
      @include tablet {
         @include mr(20px)
      }
   }
   &__left>* {
      @include mr(40px);
      @include tablet {
         @include mr(20px)
      }
   }
   &__right>*:nth-child(3n) {
      margin: 0px 25px 0px 0px;
      @include tablet {
         margin: 0px 15px 0px 0px;
      }
   }
   &__search::before {
      font-family: "Font Awesome 5 Free";
      content: "\f002";
      font-weight: 900;
      margin: 0px 13px 0px 0px;
   }
   &__list {
      @include flex-all-sb
   }
   &__item {
   }
   &__link {
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: #fff;
      @include small-tablet{
         color:#000;
      }
   }   

   &__right {
   }
   &__left {
   }
   
}
