.visit {
   background: url('../img/mailing/background.jpg') center center no-repeat;
   background-size: cover;
   &__container {
      padding: 100px 0;
      @include mobile{
         padding: 60px 0;
      }
   }

   &__body {
      padding: 0 15px;
      margin: 0 auto;
      max-width: 754px;
   }

   &__title {
      color:#fff;    
      margin: 0px 0px 15px 0px;
   }

   &__subtitle {
      color:#fff;    
      margin: 0px 0px 45px 0px;
   }

   &__text {
      font-weight: 400;
      font-size: 18px;
      line-height: 140%;
      text-align: center;
      color: #FFFFFF;
   }

   &__button {
      margin: 0 auto;
      max-width: 224px;
      height: 51px;
      text-align: center;
      background: #fff;
      transition-duration: .3s;
      &:hover{
         transition-duration: .3s;
         background: rgb(202, 202, 202);
      }
   }

   &__link {
      @include flex-all-center;
      width: 100%;
      height: 100%;
      font-weight: 500;
      font-size: 18px;
      line-height: 116%;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: #333333;
   }
}
