.contcts {

   &__container {
      padding: 0px 0px 150px 0px;
      @include mobile{
      padding: 0px 0px 60px 0px;

      }
   }

   &__contact {
      font-weight: 400;
      font-size: 21px;
      line-height: 140%;
      text-align: center;
      color: #333333;
      margin: 0px 0px 15px 0px;
   }

   &__subtitle {
      margin: 0px 0px 60px 0px;
      color: #333333;
   }

   &__body {
      margin:0 auto;
      max-width: 1110px;
      display: grid;
      grid-template-columns: repeat(4,minmax(250px,278px));
      grid-template-rows: repeat(2,1fr);
      @media (max-width: 1140px){
         grid-template-columns: repeat(2,minmax(250px,278px));
         grid-template-rows: repeat(4,1fr);
         max-width: 555px;
      }
      @include mobile{
         padding: 0 15px;
         grid-template-columns: repeat(2,minmax(145px,275px));
         grid-template-rows: repeat(4,1fr);
         max-width: 555px;
      }
   }

   &__item {
      min-width: 278px;
      max-height: 300px;
      @include mobile{
         min-width: 139px;

      }
   }

   &__img {
      object-fit: cover;
      width: 100%;
      height: 100%;
   }
}
