.mailing {
   padding: 100px 0 120px 0;
   background: url('../img/mailing/background.jpg') center center no-repeat;
   background-size: cover;
   @include tablet{
      padding: 60px 0 70px 0;
   }
   &__container {
   }

   &__title {
      color:#fff;
      margin: 0px 0px 15px 0px;
   }

   &__subtitle {
      color:#fff;
      margin: 0px 0px 60px 0px;
   }

   &__body {
      max-width: 773px;
      @include flex-all-sb;
      margin: 0 auto;
      @include tablet{
         flex-direction: column;
         align-items: center;
         justify-content: flex-start;
      }
   }

   &__list {
   }

   &__ul {
   }

   &__item {
      display: flex;
      align-items: center;
      @include mb(15px);
      font-weight: 400;
      font-size: 18px;
      line-height: 140%;
      color: #FFFFFF;
   }
   &__item::before{

      content: url('../img/mailing/play.svg');
      margin: 0px 20px 0px 0px;
   }

   &__email {
      margin: 0px 0px 0px 15px;
      padding: 15px;
      border: 1px solid #Fff;
      @include tablet{
         margin: 20px 0px 0px 0px;
      }
   }

   &__form {
      padding: 20px;
      background: #fff;
      @include mobile{
         display: flex;
         flex-direction: column;
      }
   }

   &__i1{
      border: 1px solid #D6D6D6;
      padding: 14px 20px;
      font-weight: 500;
      font-size: 14px;
      line-height: 114%;
      letter-spacing: 0.1em;
      //text-transform: uppercase;
      color: #333333;
      max-width: 200px;
      outline: none;
      @include mobile{
         margin: 0px 0px 15px 0px;
      }
      &::placeholder {
         color: rgba(51, 51, 51, 0.5);
      }
      &:focus{
      border: 1px solid #333333;

      }
   }
   &__i2{
      transition-duration: .3s;
      background: #333333;
      font-weight: 500;
      font-size: 14px;
      line-height: 114%;
      text-align: center;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: #FFFFFF;
      padding: 14px 35px;
      //@include flex-all-center;
      min-height: 45px;
      border: none;
   }
   &__i2:hover{
      transition-duration: .3s;
      cursor: pointer;
      background: #525252;
   }
}
.title {
}
.subtitle {
}
