.productItem{
   padding: 0px 0px 20px 0px;
   &__container{
      padding: 30px 30px 0px 30px;
      @include mobile{
      padding: 30px 15px 0px 15px;
   }
   }
   &-card__info{
      margin: 120px 0px 0px 0px;
   }
}

.graph-modal{

}

.productItem {

   &__container {
   }

   &__body {
      display: flex;
      justify-content: space-between;
      @include tablet{
         flex-wrap: wrap;
      }
      
   }

   &__images {
      margin: 0px 0px 0px 0px;
      flex: 0 1 530px;
      display: grid;
      grid-template-columns: 160px 160px 160px;
      grid-template-rows: 430px 130px;
      gap: 30px;
      margin: 0 auto;
      @include tablet{
         grid-template-columns: 404px 120px;
         grid-template-rows: repeat(3,95px);
         column-gap: 13px;
         row-gap: 30px;
      }
      @include mobile{
         display: flex;
         justify-content: space-between;
         //align-items: center;
         flex-wrap: wrap;
      }
      button{
      }
      
      img{
         width: 100%;
         height: 100%;
      }
   }
   .g1{
      grid-column: 1/4;
      @include tablet{
         grid-column: 1/2;
         grid-row: 1/4;
      }
      @include mobile{
         flex: 1 1 100%;
      }
   }
   .g2{
      @include tablet{
         grid-column: 2/3;
         grid-row: 1/2;
      }
      @include mobile{
         flex: 1 1 30%;
      }
   }
   .g3{
      @include tablet{
         grid-column: 2/3;
         grid-row: 2/3;
      }
      @include mobile{
         flex: 1 1 30%;
      }
   }
   .g4{
      @include tablet{
         grid-column: 2/3;
         grid-row: 3/4;
      }
      @include mobile{
         flex: 1 1 30%;
      }
   }

   &__img {
   }

   &__info {
   }
}
.info-product {
   flex: 0 1 530px;
   border: 1px solid #D6D6D6;
   padding: 10px;
   @include tablet{
      flex: 1 1 100%;
      margin: 30px 0px 0px 0px;
   }
   @include mobile{
      flex: 1 1 100%;
   }
   &__container {
      padding: 40px 30px;
      border: 1px solid #D6D6D6;
   }

   &__title {
      margin: 0px 0px 30px 0px;
      font-weight: 500;
      font-size: 24px;
      line-height: 117%;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: #333333;
      @include mobile{
         margin: 0px 0px 10px 0px;
      }
   }

   &__tags {
      display: flex;
      margin: 0px 0px 30px 0px;
      @include mobile{
         margin: 0px 0px 0px 0px;
      }
   }

   &__tag {
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      /* identical to box height, or 21px */
      color: #979797;
      &:first-child{
         margin: 0px 25px 0px 0px;
      }
   }

   &__text {
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: #333333;
      margin: 0px 0px 30px 0px;
      @include mobile{
         margin: 0px 0px 10px 0px;
      }
   }

   &__price {
      font-weight: 600;
      font-size: 24px;
      line-height: 117%;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      color: #333333;
      margin: 0px 0px 30px 0px;
      @include mobile{
         margin: 0px 0px 10px 0px;
      }
   }

   &__buttons {
      display: flex;
      @include mobile{
         flex-direction: column;
         align-items: center;
         justify-content: center;
      }
   }

   &__button {
      font-weight: 400;
      font-size: 18px;
      line-height: 21px;
      text-align: center;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      &_add{
         margin: 0px 0px 0px 30px;
         color:#333333;
         padding: 10px 20px;
         border: 1px solid #333333;
         @include mobile{
            margin: 20px 0px 0px 0px;
         }
      }
      &_buy{
         padding: 10px 20px;
         color:#fff;
         background: #333333;
      }
   }
   .hr{
      display: block;
      width: 100%;
      height: 1px;
      background: #D6D6D6;
      @include mobile{
         margin: 15px 0;
      }
   }
}

.graph-modal {
   display: none;
   z-index: 9999;
   position: fixed;
   top: 0vh;
   left: 0vw;
   height: 100vh;
   width: 100vw;
   background: rgba($color: #000000, $alpha: .7);
   &__container {
      padding: 15px;
      //height: 90%;
      //width: 80vw;
   }

   &__content {
      position: relative;
      width: 100%;
      height: 100%;
   }

   &__close {
      position: absolute;
      right: 25px;
      top: 25px;
      color: black;
      font-size: 36px;
      //margin: 20px 0px 0px 20px;
   }
   .productItem {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      &__img {
         object-fit: cover;
         width: 100%;
         border-radius: 5px;
      }
   }
}
.is-open{
   display: block;
}
.productItem {
   &__path {
      margin: 0px 0px 30px 0px;
   }
}
.path {
   max-width: 1110px;
   margin-left: auto;
   margin-right: auto;
   display: flex;
   &__link {
      @include mr(15px);
      .path__item{
         border-bottom: 1px solid #333333;
      }
   }

   &__item {
      font-weight: 400;
      font-size: 14px;
      line-height: 117%;
      display: block;
      color: #333333;
      &:last-child{
         margin: 0px 0px 0px 15px;
      }
   }
}
