.advice {
   padding: 120px 0px;
   @include mobile{
      padding: 70px 0px;
   }

   &__container {
      max-width: 1100px;
   }
   &__body {
      padding: 0px 0px 60px 0px;
   }

   &__subtitle{
      color:#333333;
   }
   &__title{
      color:#333333;
   }

   &__items {
      display: flex;
      margin: 0 -15px;
      height: 370px;
      justify-content: center;
      margin: 0 auto;
      @media (max-width: 1100px){
         flex-direction: column;
         max-width: 400px;
         height: 1170px;
      }
   }

   &__item {
      flex: 1 1 33.3333%;
      padding: 0 15px;
      height: 100%;
      @include mb(30px)
   }

   &__cont {
      height: 100%;
      width: 100%;
      position: relative;
   }

   &__img {
      z-index: 3;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
   }

   &__text {
      text-align: center;
      z-index: 10;
      position: absolute;
      bottom: 35px;
      width: 100%;
      font-weight: 400;
      font-size: 21px;
      line-height: 140%;
      text-align: center;
      color: #FFFFFF;
   }
   &__button {
      width: 255px;
      height: 51px;
      background: #333333;
      margin: 0 auto;
      a{
         height: 100%;
         width: 100%;
         @include flex-all-center;
         font-weight: 500;
         font-size: 18px;
         line-height: 117%;
         text-align: center;
         letter-spacing: 0.1em;
         text-transform: uppercase;
         color: #FFFFFF;
         transition-duration: .3s;
      }
      &:hover{
         transition-duration: .3s;
         background: #5e5e5e;
      }
   }
}
.title {
   margin: 0px 0px 15px 0px;
}
.subtitle {
   margin: 0px 0px 60px 0px;
}
